/**
 *  HTML 
 */
export function escapeHtml(text: string): string {
  return text
    .replace(/&/g, `&amp;`) //  &
    .replace(/</g, `&lt;`) //  <
    .replace(/>/g, `&gt;`) //  >
    .replace(/"/g, `&quot;`) //  "
    .replace(/'/g, `&#39;`) //  '
}

/**
 * 
 */
export function ucfirst(str: string) {
  return str.slice(0, 1).toUpperCase() + str.slice(1).toLowerCase()
}
